.ct-label.ct-label.ct-horizontal.ct-end {
  position: relative;
  justify-content: flex-end;
  text-align: right;
  -webkit-transform-origin: 100% 0;
          transform-origin: 100% 0;
  -webkit-transform: translate(-100%) rotate(-45deg);
          transform: translate(-100%) rotate(-45deg);
  white-space:nowrap;
}
